import {request} from"../../request.js"
export default {
	data() {
		return {
			checked:false,
			discountamount:"",
			amount:"",
		};
	},
	methods: {
		changeCheck() {
			this.checked = !this.checked
		},
		// 买单页面
		goPay() {
			if (this.amount <= 0 ){
				uni.showToast({
					title:"总金额不能为0",
					icon:"none",
				})
			}
			request({
				url:"index.php?s=/wap/order/offlineOrderSetSession",
				data:{
					shop_id:27,
					amount:this.amount,
					discountamount:this.discountamount,
				}
			}).then(res=>{
				if(res.data==1){
					uni.navigateTo({
						url:"/pages/components/checkstand/checkstand",
					})
				}
			})
			
		},
		/**
		 *	记录金额
		 */
		setOrderSession(){
			
		}
	}
}
